input, textarea{
    color: white
}

/* navbar */

.navbar{
    padding-top: 2em;
    margin-bottom: 1em;
    color: white;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

.nav-items{ float: right }

.logo{
    max-height: 50px;
    width: auto;
}

/* footer */

.footer{
    background: rgb(20, 20, 20);
    padding-top: 3rem;
    padding-bottom: 3rem;
}

/* main page */

.hero{
    background: black;
    min-height: 450px;
    color: white;
}

.hero-text{ color: rgb(153, 153, 153); }

.hero-video{
    width: 100%;
    max-height: 100%;
    position: relative;
    top: 0;
    left: 0;
}

.hero-interaction{
    position: absolute;
    width: 150px;
    max-height: auto;
    top: 300px;
    left: 60px;
}

.bounce{
  display:block;
  -webkit-animation: 4s infinite alternate floating;
          animation: 4s infinite alternate floating;
}

.shadow{
  height:2px;
  display:block;
  margin:5px auto;
  -webkit-animation: 4s infinite alternate shadow;
          animation: 4s infinite alternate shadow;
}

@-webkit-keyframes floating {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-5px);
		        transform: translatey(-5px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes floating {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-5px);
		        transform: translatey(-5px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

/* info section */

.info{
    font-size: 0.9em;
    margin-bottom: 2em;
    border-top: solid 1px rgb(20, 20, 20);
    color: rgb(153, 153, 153)
}

.info-column{
    width: 20em;
    margin-left: auto;
    margin-right: auto;
}

.info-img{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background: rgb(20, 20, 20);
    border-radius: 50%;
    display: block;
    margin-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
}

.info-title{ color: #9b4dca; }

/* button */

.btn{
    color: white;
    border-radius: .4rem;
    font-size: 1.1rem;
    font-weight: 700;
    height: 3.8rem;
    letter-spacing: .1rem;
    line-height: 3.8rem;
    padding: 0 3.0rem;
    text-transform: uppercase;
}

.btn-link{
    padding: 1rem 3rem;
    background: none;
    border: 1px solid white;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.btn-primary {
    border: none;
    background-size: 150% auto;
    background-image: -webkit-gradient(linear, left top, right top, from(#9b4dca), color-stop(51%, rgb(78, 69, 197)), to(rgb(41, 125, 194)));
    background-image: linear-gradient(to right, #9b4dca 0%, rgb(78, 69, 197) 51%, rgb(41, 125, 194) 100%);
}

.btn-tg:hover{
    background: rgba(30, 75, 117, 0.438);
    /* border: 1px solid rgb(30, 75, 117); */
    color: white;
}

.btn-ws:hover{
    background: rgba(38, 138, 91, 0.281);
    /* border: 1px solid rgb(43, 109, 78); */
    color: white;
}

.btn-gm:hover{
    background: rgba(206, 60, 60, 0.288);
    /* border: 1px solid rgb(122, 38, 38); */
    color: white;
}

.btn-icon{
    height: 1em;
    width: auto;
    padding-right: 1em;
}

